.modal {
  background-color: var(--background-lightsand);
  min-width: 320px;
  height: calc(100% - 200px);
  border-radius: 24px;
  padding: 16px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 8px 30px rgba(212, 20, 67, .1);
}

.modal__backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 999;
  background-color: rgba(255, 165, 186, .6);
  transition: opacity .5s, visibility .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
  transform: scale(1);
}

.modal__backdrop.modal__is-hidden {
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  transform: scale(0);
}

.modal__backdrop.modal__is-hidden .modal {
  transform: translate(-50%, -50%)scale(1.2);
}

@media screen and (max-width: 479px) {
  .modal {
    width: calc(100% - 30px);
  }
}

@media screen and (min-width: 480px) {
  .modal {
    width: 450px;
  }
}

@media screen and (min-width: 768px) {
  .modal {
    width: 630px;
  }
}

.modal::-webkit-scrollbar {
